#ExperienceCard-wrapper {
  display: grid;
  grid-template-columns: 25% 70%;
  grid-template-rows: 20vw 1vw 1vw;
  grid-gap: 5%;
  color: white;
  font-size: 2vmin;
  padding: 1.5em;
  text-align: left;
}

main,
aside {
  background: #B31B1B;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 2em;
}


